	module.exports = {"data":{"post":{"html":"<p>Christopher is 13 weeks old today, which happens to also be exactly 3 months. Time to start counting in months instead of weeks! </p>\n<p>He's starting to have some loud crying phases. Our first few weeks of the quiet baby with no problems to speak of were indeed too good to be true. Is he teething? Maybe... anyway, he and Mama are exhausted this evening. She won't be flipping her home page today.</p>\n<p>Meanwhile, we're getting ready for my folks to visit their new grandson for 2 1/2 weeks starting on Friday. We're having Christopher baptized this Sunday, and otherwise we have no fixed plans for their stay. We can't make any big trips... niether of our cars can accomodate 4 adults and a child seat!</p>","fields":{"title":"13 Weeks = 3 Months","date":"27 Mar 2000","url":"https://papascott.de/2000-03-27-13-weeks-3-months/"}}},"pathContext":{"slug":"/2000-03-27-13-weeks-3-months/","prev":{"fields":{"title":"Daylight Savings Time Sunday","date":"26 Mar 2000","slug":"/2000-03-26-daylight-savings-time-sunday/"}},"next":{"fields":{"title":"We're No Angels","date":"28 Mar 2000","slug":"/2000-03-28-were-no-angels/"}}}}

